body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Noto Sans TC;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-flow:row wrap;
  align-items: center;
  justify-content: center;
}

.card{
  width: 80vw;
  max-width: 600px;
  height: 89vh;
  box-shadow: 0 0 3px 1px rgba(51, 51, 51, 0.5);
  background: #fff;
  border-radius: 8px;
  line-height: 1.4;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  gap:12px;
}

.old-date,
.start-date{
width: 80%;
}

input{
  width: 100%;
  height: 4vh;
  background: #F7F8F9;
  border-radius: 8px;
  border: 2px solid #E8ECF4;
}

input:focus{
  border: 2px solid #2E7CF6;
}

.hint{
  color:#F00
}

.change-btn{
  width: 80%;
  height: 5vh;
  border-radius: 8px;
  background: #E5613B;
  border: none;
  outline: none;
}

.change-btn:active{
background: #ce5735;
}
